.primary-button {
  background-color: #68b738;
  color: #ffffff;
  font-family: "Oswald-Regular", "Arial", sans-serif;
  font-weight: 400;
  font-style: normal;
  text-transform: uppercase;
  text-align: center;
  font-size: 16px;
  line-height: 20px;
  border: 0;
  padding: 10px;
  min-height: 40px;
  text-decoration: none;
  cursor: pointer;
}
.primary-button_index-page {
  font-size: 14px;
  line-height: 18px;
}
.primary-button_form-page {
  font-size: 16px;
  line-height: 20px;
}
.primary-button:hover {
  background-color: #5eaa2f;
}
.primary-button:active {
  color: rgba(255, 255, 255, 0.3);
}
.primary-button_gray {
  color: #444444;
  background-color: #f2f2f2;
}
.primary-button_gray:hover {
  background-color: #EBEBEB;
}
.primary-button_gray:active {
  color: rgba(68, 68, 68, 0.3);
}
@media (min-width: 768px) {
  .primary-button_index-page {
    font-size: 20px;
    line-height: 20px;
    min-height: 54px;
    padding: 16px 26px 13px;
  }
  .primary-button_form-page {
    font-size: 20px;
    line-height: 0;
    min-height: 52px;
  }
}
.radio-button {
  list-style: none;
  margin: 0;
  padding: 0 0 1px 0;
}
.radio-button li {
  margin-bottom: 13px;
}
.radio-button li:last-child {
  margin-bottom: 8px;
}
.radio-button input {
  position: absolute;
  width: 0;
  height: 0;
}
.radio-button label {
  position: relative;
  display: inline-block;
  vertical-align: top;
  cursor: pointer;
  font-family: "Oswald-Regular", "Arial", sans-serif;
  font-weight: 400;
  font-style: normal;
  font-size: 16px;
  line-height: 18px;
  text-transform: uppercase;
  color: #222222;
  padding-left: 45px;
  padding-top: 6px;
  padding-bottom: 6px;
}
.radio-button label::after {
  position: absolute;
  content: "";
  width: 30px;
  height: 30px;
  left: 0;
  top: 0;
  border: 1px solid #cdcdcd;
  border-radius: 50%;
}
.radio-button input:checked + label::before {
  position: absolute;
  content: "";
  width: 14px;
  height: 14px;
  background-color: #68b738;
  border-radius: 50%;
  top: 8px;
  left: 8px;
  z-index: 10;
}
.radio-button label:hover::after {
  border-color: #BABABA;
}
.radio-button label:active {
  opacity: 30%;
}
.radio-button input:focus + label::after {
  border-color: #444444;
  border-width: 2px;
}
@media (min-width: 768px) {
  .radio-button li {
    margin-bottom: 12px;
  }
  .radio-button label {
    font-size: 20px;
    line-height: 30px;
    padding-left: 60px;
    padding-top: 5px;
  }
  .radio-button label::after {
    width: 40px;
    height: 40px;
  }
  .radio-button input:checked + label::before {
    width: 18px;
    height: 18px;
    top: 11px;
    left: 11px;
  }
}
.checkbox__item input {
  position: absolute;
  width: 0;
  height: 0;
}
.checkbox__item label {
  position: relative;
  display: block;
  vertical-align: top;
  cursor: pointer;
  font-family: "Oswald-Regular", "Arial", sans-serif;
  font-weight: 400;
  font-style: normal;
  font-size: 16px;
  line-height: 18px;
  text-transform: uppercase;
  color: #222222;
  padding-left: 45px;
  padding-top: 6px;
  padding-bottom: 6px;
}
.checkbox__item label::after {
  position: absolute;
  content: "";
  width: 30px;
  height: 30px;
  left: 0;
  top: 0;
  border: 1px solid #cdcdcd;
}
.checkbox__item input:checked + label::before {
  position: absolute;
  content: "";
  width: 26px;
  height: 26px;
  background-image: url("../img/checkbox-tick.svg");
  background-repeat: no-repeat;
  background-position: center center;
  top: 2px;
  left: 2px;
  z-index: 10;
}
.checkbox__item input:disabled + label {
  opacity: 0.3;
}
.checkbox__item label:hover::after {
  border-color: #BABABA;
}
.checkbox__item label:active {
  opacity: 30%;
}
.checkbox__item input:focus + label::after {
  border-color: #444444;
  border-width: 2px;
}
@media (min-width: 768px) {
  .checkbox__item label {
    font-size: 20px;
    line-height: 30px;
    padding-left: 60px;
    padding-top: 5px;
    padding-bottom: 6px;
  }
  .checkbox__item label::after {
    width: 40px;
    height: 40px;
  }
  .checkbox__item input:checked + label::before {
    width: 18px;
    height: 18px;
    top: 11px;
    left: 11px;
  }
}
.field__wrapper {
  display: flex;
  align-items: flex-start;
}
.field__name {
  margin-top: 15px;
  margin-right: 16px;
  font-family: "Oswald-Regular", "Arial", sans-serif;
  font-weight: 400;
  font-style: normal;
  text-transform: uppercase;
  font-size: 16px;
  line-height: 18px;
}
.field__name_phone-email {
  margin-top: 10px;
  margin-right: 12px;
}
.field {
  border: 1px solid #e7e7e7;
  padding: 12px 15px;
  color: #444444;
  text-transform: uppercase;
}
.field:hover {
  border-color: #cdcdcd;
}
.field::placeholder {
  color: #444444;
}
.field:not(:placeholder-shown) {
  border-color: #FF8282;
  border-width: 2px;
  outline: none;
}
.field__input_email:not(:placeholder-shown) {
  background-image: url("../img/icon-mail-error.svg");
}
.field:disabled {
  background-color: white;
  border-color: #444444;
  box-shadow: inset 0 0 0 1px #444444;
  outline: none;
}
.field__email-icon:disabled {
  background-image: url("../img/icon-mail-disabled.svg");
}
.field__phone-icon:disabled {
  background-image: url("../img/icon-phone-disabled.svg");
}
.field:valid {
  border-color: #e7e7e7;
  border-width: 1px;
}
.field__input {
  flex-grow: 1;
  height: 45px;
  font-family: "Oswald-Regular", "Arial", sans-serif;
  font-weight: 400;
  font-style: normal;
  font-size: 16px;
  line-height: 18px;
}
.field__with-icon {
  border: 1px solid #cdcdcd;
  padding-right: 50px;
  background-repeat: no-repeat;
}
.field__with-icon:hover {
  border-color: #cdcdcd;
}
.field__email-icon {
  background-image: url("../img/icon-mail.svg");
  background-size: 19px 14px;
  background-position: right 15px top 15px;
}
.field__phone-icon {
  background-image: url("../img/icon-phone.svg");
  background-size: 21px 21px;
  background-position: right 12px top 13px;
}
.field:focus {
  border-color: #444444;
  box-shadow: inset 0 0 0 1px #444444;
  outline: none;
}
.field__textarea {
  flex-grow: 1;
  height: 150px;
  font-family: "Oswald-Regular", "Arial", sans-serif;
  font-weight: 400;
  font-style: normal;
  font-size: 16px;
  line-height: 18px;
}
@media (min-width: 768px) {
  .field {
    padding: 10px 18px;
  }
  .field__name {
    font-size: 20px;
    line-height: 30px;
    margin-top: 12px;
  }
  .field__input {
    height: 52px;
    font-size: 20px;
    line-height: 30px;
  }
  .field__email-icon {
    padding-right: 60px;
    background-size: 18px 13px;
    background-position: right 19px top 19px;
  }
  .field__phone-icon {
    padding-right: 60px;
    background-position: right 17px top 15px;
  }
  .field__textarea {
    height: 174px;
    font-size: 20px;
    line-height: 30px;
  }
}
.social {
  display: flex;
  align-content: center;
  list-style: none;
